import { Location } from '@angular/common';
import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { NotificationService } from 'src/app/shared/notification.service';
import { ApiService } from '../../../shared/api.service';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css'],
})
export class AccountComponent implements OnInit {
  @Input()
  accountid;
  account;

  constructor(
    private api: ApiService,
    private notificationService: NotificationService,
    private activatedroute: ActivatedRoute,
    private router: Router,
    private location: Location
  ) {}

  async ngOnInit() {
    this.account = await this.api
      .get(`/api4/accounts/${this.accountid}`)
      .toPromise();
  }

  async save(account) {
    await this.api.post(`/api4/accounts`, account).toPromise();
    this.notificationService.alert('Account saved');
    // this.router.navigate(['..'], { relativeTo: this.route });
  }

  async remove(accountid) {
    if (confirm('Deleting record. Are you sure ?')) {
      const result = (await this.api
        .delete(`/api4/accounts/${accountid}`)
        .toPromise()) as any;
      // this.router.navigate(['..'], { relativeTo: this.route });
      this.notificationService.alert(result.message);
      this.location.back();
    }
  }

  back() {
    this.location.back();
  }
}
